/*
    Colorbox Core Style:
    The following CSS is consistent between example themes and should not be altered.
*/
#colorbox, #cboxOverlay, #cboxWrapper{position:absolute; top:0; left:0; z-index:9999; overflow:hidden;}
#cboxWrapper {max-width:none;}
#cboxOverlay{position:fixed; width:100%; height:100%;}
#cboxMiddleLeft, #cboxBottomLeft{clear:left;}
#cboxContent{position:relative;}
#cboxLoadedContent{overflow:auto; -webkit-overflow-scrolling: touch;}
#cboxTitle{margin:0;}
#cboxLoadingOverlay, #cboxLoadingGraphic{position:absolute; top:0; left:0; width:100%; height:100%;}
#cboxPrevious, #cboxNext, #cboxClose, #cboxSlideshow{cursor:pointer;}
.cboxPhoto{float:left; margin:auto; border:0; display:block; max-width:none; -ms-interpolation-mode:bicubic;}
.cboxIframe{width:100%; height:100%; display:block; border:0; padding:0; margin:0;}
#colorbox, #cboxContent, #cboxLoadedContent{box-sizing:content-box; -moz-box-sizing:content-box; -webkit-box-sizing:content-box;}

/* 
    User Style:
    Change the following styles to modify the appearance of Colorbox.  They are
    ordered & tabbed in a way that represents the nesting of the generated HTML.
*/
#cboxOverlay{background:#000; opacity: 0.9; filter: alpha(opacity = 90);}
#colorbox{outline:0;}
#cboxTopLeft{width:14px; height:14px; background:url(img/controls.png) no-repeat 0 0;}
#cboxTopCenter{height:14px; background:url(img/border.png) repeat-x top left;}
#cboxTopRight{width:14px; height:14px; background:url(img/controls.png) no-repeat -36px 0;}
#cboxBottomLeft{width:14px; height:43px; background:url(img/controls.png) no-repeat 0 -32px;}
#cboxBottomCenter{height:43px; background:url(img/border.png) repeat-x bottom left;}
#cboxBottomRight{width:14px; height:43px; background:url(img/controls.png) no-repeat -36px -32px;}
#cboxMiddleLeft{width:14px; background:url(img/controls.png) repeat-y -175px 0;}
#cboxMiddleRight{width:14px; background:url(img/controls.png) repeat-y -211px 0;}
#cboxContent{background:#fff; overflow:visible;}
.cboxIframe{background:#fff;}
#cboxError{padding:50px; border:1px solid #ccc;}
#cboxLoadedContent{margin-bottom:5px;}
#cboxLoadingOverlay{background:url(img/loading_background.png) no-repeat center center;}
#cboxLoadingGraphic{background:url(img/loading.gif) no-repeat center center;}
#cboxTitle{position:absolute; bottom:-25px; left:0; text-align:center; width:100%; font-weight:bold; color:#7C7C7C;}
#cboxCurrent{position:absolute; bottom:-29px; left:58px; font-weight:bold; color:#7C7C7C;}

/* these elements are buttons, and may need to have additional styles reset to avoid unwanted base styles */
#cboxPrevious, #cboxNext, #cboxSlideshow, #cboxClose {border:0; padding:0; margin:0; overflow:visible;  position:absolute; bottom:-29px; background:url(img/controls.png) no-repeat 0px 0px; width:23px; height:23px; text-indent:-9999px;}

/* avoid outlines on :active (mouseclick), but preserve outlines on :focus (tabbed navigating) */
#cboxPrevious:active, #cboxNext:active, #cboxSlideshow:active, #cboxClose:active {outline:0;}

#cboxPrevious{left:0px; background-position: -51px -25px;}
#cboxPrevious:hover{background-position:-51px 0px;}
#cboxNext{left:27px; background-position:-75px -25px;}
#cboxNext:hover{background-position:-75px 0px;}
#cboxClose{right:0; background-position:-100px -25px;}
#cboxClose:hover{background-position:-100px 0px;}

.cboxSlideshow_on #cboxSlideshow{background-position:-125px 0px; right:27px;}
.cboxSlideshow_on #cboxSlideshow:hover{background-position:-150px 0px;}
.cboxSlideshow_off #cboxSlideshow{background-position:-150px -25px; right:27px;}
.cboxSlideshow_off #cboxSlideshow:hover{background-position:-125px 0px;}


#cboxTopCenter {
    background: #fff;
    height: 4px;
}
#cboxTopLeft,
#cboxTopRight {
    background: #fff;
    height: 4px;
    width: 4px;
}
#cboxMiddleLeft,
#cboxMiddleRight {
    background: #fff;
    //border-left: 4px solid #fff;
    box-sizing: border-box;
    width: 4px;
}
#cboxBottomLeft,
#cboxBottomRight {
    background: transparent;
    width: 4px;
}
#cboxBottomCenter {
    background: transparent;
}
#cboxClose {
    background-image: url('img/close.png');
    background-position: center;
    bottom: -34px;
    height: 27px;
    width: 27px;
    &:hover,
    &:focus,
    &:active {
        background-position: center;
    }
}
#cboxNext,
#cboxPrevious {
    bottom: auto;
    height: 100%;
    outline: none;
    top: 0;
    transition: opacity .3s;
    width: 50%;
    @media (min-width: 992px) {
        opacity: 0;
        &:hover {
            opacity: 1;
        }
    }

}
#cboxNext {
    background-image: url('img/next.png');
    background-position: right center;
    left: auto;
    right: 0;
    &:hover,
    &:focus {
        background-position: right center;
        outline: none;
    }
}
#cboxPrevious {
    background-image: url('img/prev.png');
    background-position: left center;
    left: 0;
    right: auto;
    &:hover,
    &:focus {
        background-position: left center;
        outline: none;
    }
}
#cboxCurrent {
    bottom: -34px;
    color: #fff;
    left: 0;
}
