.wpcf7-form label small, .foto_upload, .schriftliche_zustimmung, .chapter{
    margin-top: 20px;
    display: block;
}

.wpcf7-form label small{
    line-height: 1.5;
}

.wpcf7-form label.option{
    display: inline-block;
    margin-top: 5px;
    margin-bottom: 10px;
}

input[type=checkbox] {
    margin-right: 15px;
}

body .wpcf7-form p {
    line-height: 1.3 !important;
}

body label[for=accept_id]{
    margin-top: 20px;
}

.page-id-202 .hero__box{
    margin-top: 0;
}

.footer-nav.socials li a{
    font-size: 14px;
}

@media (max-width: 480px) {
    .page-startseite .hero__headline{
        padding-left: 40px;
        padding-right: 40px;
    }
}



.em-booking-form .input-group.input-radio {
    display: block;
    width: 100%;
    margin-bottom: 30px;
    margin-top: 0;
}

.em-booking-form .input-radio.input-field-anrede .input-group {
    margin-left: 0px;
}

.em-booking-form span.input-group input {
    margin-left: 0;
}

.em-booking-form .input-field-dbem_member {
    display: block;
    width: 100%;
}

.em-booking-form .input-field-dbem_member select {
    padding: .6rem;
    width: 273px;
    border: 1px solid #232121;
}

.gform_wrapper {
    font-size: 1.125rem;
    margin: 0 auto;
    max-width: 640px;
}

.gfield_radio li {
    display: inline-block;
}

.gform_wrapper input[type=text] {
    -webkit-appearance: none!important;
    background: #fff;
    border: 1px solid #232121!important;
    -webkit-border-radius: 0;
    border-radius: 0;
    display: block;
    padding: .6rem .6rem!important;
    width: 100% !important;
    line-height: 1.925rem;
}

.gform_wrapper select {
    background: #fff;
    border: 1px solid #232121!important;
    -webkit-border-radius: 0;
    border-radius: 0;
    display: block;
    padding: .7rem .6rem!important;
    width: 100% !important;
    line-height: 1.925rem;
}

.gform_wrapper input[type=submit] {
    background-color: #f8a102;
    background-image: url(data:image/svg+xml;base64,) !important;
    background-position: right 1.2rem center;
    background-repeat: no-repeat;
    background-size: .6em;
    border-color: #f8a102;
    color: #fff !important;
    padding-right: 2.4em;
    display: block!important;
    margin-left: auto!important;
}

body .gform_wrapper ul li.gfield {
    margin-top: 25px;
    padding-top: 0;
}

.gform_wrapper .gfield_radio label {
    color: #6e6e6e;
    display: block;
    line-height: 1.875rem;
}

.gform_wrapper .gfield_radio label, .gform_wrapper .js-form-type-checkbox label, .webform .js-form-type-radio label, .webform .js-form-type-checkbox label {
    padding-left: 10px!important;
    margin-right: 20px!important;
    position: relative!important;
}

.gform_wrapper input[type='radio'] {
    transform: scale(1.4);
    margin-left: 5px!important;
}

.card__footer a.card__cta {
    white-space: nowrap;
}

.em-location-map-container *,
.em-location-map-container ::before,
.em-location-map-container ::after {
    -webkit-transition: none !important;
    -o-transition: none !important;
    -moz-transition: none !important;
    transition: none !important;
}

.card.has-link {
    cursor: auto !important;
}

.single-event .card.is-regular + i {
    display: none;
}

.page-veranstaltungen .em-pagination {
    text-align: center;
}

.single-event .card__image-wrapper {
    align-items: flex-end;
}

@media (max-width: 1024px){
    .single-event .card__image-wrapper {
        align-items: flex-start;
        overflow: hidden;
    }
} 
.single-event .card__image-wrapper img {
    object-fit: contain;
    max-height: inherit;
}

.veranstaltungskategorieliste {
    margin-top: 3rem;
}

.veranstaltungskategorielisteHeadline {
    display: none;
}